export default function BlogPage() {
  const { theme } = useContext(ThemeContext);
  //const { locale, defaultLang, config } = useLocalization();
  const { t, i18n } = useTranslation('blog');

  return (
    <Page
      title={t('Blog')}
      keywords={[
        'developer', 'development',
        'blockchain', 'crypto', 'ethereum', 'web3',
        'nft', 'smart contract', 'token',
      ]}
    >
      <div id={`front ${theme}`} style={{ minHeight: '95vh', }}>
        {/*<modal v-if="open" />*/}
        <main className="main">
          <section id='home' className="section section-start">
            <div className="container">
              <div className="row">
                <div className="col-12 col-md-7 col-lg-6 col-xl-6 relative">
                  <div className="pre-title">
                    <span>{t('Aleksandr Ivannikov')}</span>
                  </div>
                  <h1 className="">
                    {t('Blog')}
                  </h1>
                  <p className="desc">{t('Blog')}</p>
                  <LinkExternal
                    href={`mailto:${email}`}
                  >
                    <CommonButton
                      variant="gradient"
                      size="static"
                      className=""
                    //@click="toggle"
                    >{t('Get in Touch')}</CommonButton>
                  </LinkExternal>
                </div>
                <div className="col-12 col-md-5 col-lg-6 col-xl-6">
                  <div className="start__image">
                    <img
                      alt='IVANNIKOV.PRO'
                      title='IVANNIKOV.PRO'
                      placeholder='blurred'
                      src={ImageMain}
                    />
                    {/*<StaticImage
                      alt='IVANNIKOV.PRO'
                      title='IVANNIKOV.PRO'
                      placeholder='blurred' // blurred dominantColor 
                      layout='fixed'
                      //src={ImageMain}
                      src={'../assets/images/main.svg'}
                    //width={250}
                    //height={250}
                    />*/}
                  </div>
                </div>
              </div>
            </div>
            <br />
          </section>

          {/*<ScreenProjects />
          <ScreenBlog />*/}
        </main>
      </div>
    </Page>
  );
}


